import { Typography, Button } from "@mui/material";

import HomepageHeroLayout from "./HeroLayout";

import backgroundImage from "../img/photo-1600573472591-ee6b68d14c68.jpg?auto=format&fit=crop&w=1400&q=80"
import pairio_white from '../img/white logo.png'

export default function HomepageHero() {
    return (
        <HomepageHeroLayout sxBackground={{
            backgroundImage: `url(${backgroundImage})`,
            backgroundColor: '#7fc7d9', // Average color of the background image.
            backgroundPosition: 'center',
        }}>
            {/* Increase the network loading priority of the background image. */}
            <img
                style={{ display: 'none' }}
                src={backgroundImage}
                alt="increase priority"
            />
            <Typography color="inherit" align="center" variant="h1" marked="center">
                <img src={pairio_white} width="400px" style={{
                    position: "relative",
                    top: "25px",
                    bottom: "15px"
                }} />
            </Typography>
            <Typography
                color="inherit"
                align="center"
                variant="h5"
                sx={{ mb: 4, mt: { sx: 4, sm: 10 } }}
            >
                Pairing you with the right people for your next project
            </Typography>
            <Button
                color="primary"
                variant="contained"
                size="large"
                component="a"
                href="/premium-themes/onepirate/sign-up/"
                sx={{ minWidth: 200 }}
            >
                Find out more
            </Button>
        </HomepageHeroLayout>
    );
}