import { Container, Grid, Typography, Paper } from "@mui/material";


export default function HomepageUserSelection() {

    return (
        <Container maxWidth='lg'>
            <Grid container justifyContent="space-evenly" direction="row" spacing={{ md: 3, xs: 0.5 }}>
                <Grid item md={4} xs={10}>
                    <Paper sx={{
                        my: 3,
                        py: 6,
                        bgcolor: "primary.main",
                        '&:hover': {
                            bgcolor: 'primary.light',
                            opacity: [0.9, 0.8, 0.7]
                        },
                        textAlign: "center"
                    }}>

                        <Typography color="white" textAlign="center" my={2} variant="button">
                            Landlords
                        </Typography>

                    </Paper>
                </Grid>

                <Grid item md={4} xs={10}>
                    <Paper sx={{
                        my: 3,
                        py: 6,
                        bgcolor: "primary.main",
                        '&:hover': {
                            bgcolor: 'primary.light',
                            opacity: [0.9, 0.8, 0.7]
                        },
                        textAlign: "center",

                    }}>

                        <Typography color="white" textAlign="center" variant="button">
                            Trades
                        </Typography>
                    </Paper>
                </Grid>

                <Grid item md={4} xs={10}>
                    <Paper sx={{
                        my: 3,
                        py: 6,
                        bgcolor: "primary.main",
                        '&:hover': {
                            bgcolor: 'primary.light',
                            opacity: [0.9, 0.8, 0.7]
                        },
                        textAlign: "center"
                    }}>

                        <Typography color="white" my={2} variant="button">
                            Tenants
                        </Typography>

                    </Paper>
                </Grid>
            </Grid>
        </Container >

    )
}